<template>
	<div class="edit_view" :style='{}'>
        <div class="breadcrumb-wrapper" style="width: 100%;">
            <div class="bread_view">
                <el-breadcrumb separator="" class="breadcrumb">
                    <el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }"></el-breadcrumb-item>
                    <el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
                </el-breadcrumb>
            </div>
        </div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item label="orderid" prop="orderid">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.orderid?true:false"
							v-model="form.orderid" 
							placeholder="orderid"
							style="width:100%;"
							@change="orderidChange">
							<el-option v-for="(item,index) in orderidLists" :label="item" :value="item">
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="goodname">
						<el-input class="list_inp" v-model="form.goodname" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.goodname?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="buynumber">
						<el-input class="list_inp" v-model="form.buynumber" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.buynumber?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="total">
						<el-input class="list_inp" v-model="form.total" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.total?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="zhanghao">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.zhanghao?true:false"
							v-model="form.zhanghao" 
							placeholder=""
							style="width:100%;"
							@change="zhanghaoChange">
							<el-option v-for="(item,index) in zhanghaoLists" :label="item" :value="item">
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="mingcheng">
						<el-input class="list_inp" v-model="form.mingcheng" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.mingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="dingdanzhuangtai">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.dingdanzhuangtai?true:false"
							v-model="form.dingdanzhuangtai" 
							placeholder=""
							style="width:100%;"
							>
							<el-option v-for="(item,index) in dingdanzhuangtaiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="shangjiazhanghao">
						<el-input class="list_inp" v-model="form.shangjiazhanghao" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiazhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="shangjiamingcheng">
						<el-input class="list_inp" v-model="form.shangjiamingcheng" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiamingcheng?true:false" />
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick"></el-button>
				<el-button class="formModel_confirm"
                           @click="save"
                           type="success"
				>
					
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
    import moment from "moment";
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//
	const tableName = 'wodeyuyue'
	const formName = ''
	//
	const breadList = ref([{
		name: formName
	}])
	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form
	const form = ref({
		orderid: '',
		goodname: '',
		buynumber: '',
		total: '',
		zhanghao: '',
		mingcheng: '',
		dingdanzhuangtai: '',
		shangjiazhanghao: '',
		shangjiamingcheng: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		orderid : false,
		goodname : false,
		buynumber : false,
		total : false,
		zhanghao : false,
		mingcheng : false,
		dingdanzhuangtai : false,
		shangjiazhanghao : false,
		shangjiamingcheng : false,
	})
	const isAdd = ref(false)
	//
	const rules = ref({
		orderid: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		goodname: [
		],
		buynumber: [
		],
		total: [
		],
		zhanghao: [
		],
		mingcheng: [
		],
		dingdanzhuangtai: [
		],
		shangjiazhanghao: [
		],
		shangjiamingcheng: [
		],
	})
	//orderid
	const orderidLists = ref([])
	//
	const zhanghaoLists = ref([])
	//
	const dingdanzhuangtaiLists = ref([])
	//methods

	//methods
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='orderid'){
					form.value.orderid = row[x];
					disabledForm.value.orderid = true;
					continue;
				}
				if(x=='goodname'){
					form.value.goodname = row[x];
					disabledForm.value.goodname = true;
					continue;
				}
				if(x=='buynumber'){
					form.value.buynumber = row[x];
					disabledForm.value.buynumber = true;
					continue;
				}
				if(x=='total'){
					form.value.total = row[x];
					disabledForm.value.total = true;
					continue;
				}
				if(x=='zhanghao'){
					form.value.zhanghao = row[x];
					disabledForm.value.zhanghao = true;
					continue;
				}
				if(x=='mingcheng'){
					form.value.mingcheng = row[x];
					disabledForm.value.mingcheng = true;
					continue;
				}
				if(x=='dingdanzhuangtai'){
					form.value.dingdanzhuangtai = row[x];
					disabledForm.value.dingdanzhuangtai = true;
					continue;
				}
				if(x=='shangjiazhanghao'){
					form.value.shangjiazhanghao = row[x];
					disabledForm.value.shangjiazhanghao = true;
					continue;
				}
				if(x=='shangjiamingcheng'){
					form.value.shangjiamingcheng = row[x];
					disabledForm.value.shangjiamingcheng = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('zhanghao') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.zhanghao = json.zhanghao
				disabledForm.value.zhanghao = true;
			}
			if(json.hasOwnProperty('mingcheng') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.mingcheng = json.mingcheng
				disabledForm.value.mingcheng = true;
			}
			if(json.hasOwnProperty('shangjiazhanghao') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.shangjiazhanghao = json.shangjiazhanghao
				disabledForm.value.shangjiazhanghao = true;
			}
			if(json.hasOwnProperty('shangjiamingcheng') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.shangjiamingcheng = json.shangjiamingcheng
				disabledForm.value.shangjiamingcheng = true;
			}
            if (localStorage.getItem('autoSave')) {
                localStorage.removeItem('autoSave')
                save()
            }
		})
		context?.$http({
			url: `option/orders/orderid`,
			method: 'get'
		}).then(res=>{
			orderidLists.value = res.data.data
		})
		//
		disabledForm.value.goodname = true;
		//
		disabledForm.value.buynumber = true;
		//
		disabledForm.value.total = true;
		context?.$http({
			url: `option/yonghu/zhanghao`,
			method: 'get'
		}).then(res=>{
			zhanghaoLists.value = res.data.data
		})
		//
		disabledForm.value.mingcheng = true;
		dingdanzhuangtaiLists.value = ",,".split(',')
	}
	//
	//
	const backClick = () => {
		history.back()
	}
	//orderid
	const orderidChange=()=>{
		context?.$http({
			url: `follow/orders/orderid?columnValue=` + form.value.orderid,
			method: 'get'
		}).then(res=>{
			//
			if(res.data.data.goodname){
				form.value.goodname = res.data.data.goodname
			}
			//
			if(res.data.data.buynumber){
				form.value.buynumber = res.data.data.buynumber
			}
			//
			if(res.data.data.total){
				form.value.total = res.data.data.total
			}
			//
			if(res.data.data.mingcheng){
				form.value.mingcheng = res.data.data.mingcheng
			}
		})
	}
	//
	const zhanghaoChange=()=>{
		context?.$http({
			url: `follow/yonghu/zhanghao?columnValue=` + form.value.zhanghao,
			method: 'get'
		}).then(res=>{
			//
			if(res.data.data.goodname){
				form.value.goodname = res.data.data.goodname
			}
			//
			if(res.data.data.buynumber){
				form.value.buynumber = res.data.data.buynumber
			}
			//
			if(res.data.data.total){
				form.value.total = res.data.data.total
			}
			//
			if(res.data.data.mingcheng){
				form.value.mingcheng = res.data.data.mingcheng
			}
		})
	}
	//
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
                                context?.$toolUtil.message(``,'success')
                                history.back()
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
                        context?.$toolUtil.message(``,'success')
                        history.back()
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
        if(type.value == 'cross'){
            context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
    onUnmounted(()=>{
        Object.keys(localStorage).map(item=>{
            if(item.startsWith('cross')){
                localStorage.removeItem(item)
            }
        })
    })
</script>
<style lang="scss" scoped>
	// 
	.bread_view {
		:deep(.breadcrumb) {
			.el-breadcrumb__separator {
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
				}
			}
		}
	}
	// 
	.add_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {

				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.formModel_cancel {
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
		}
		.formModel_confirm:hover {
		}
	}
</style>
<style>
.edit_view {
    width: 1200px;
    margin: 20px auto;
    padding: 40px 20px 20px ;
    background: #fff;
    overflow: hidden;
    border: 0px solid #eee;
    font-size: 16px;
    color:#666;
}
.edit_view .add_form{
    width: 100%;
    padding: 30px;
    border:0px solid #eee
}
.edit_view .add_form .el-form-item{
    margin: 0px 0px 20px;
    display: flex;
}
.edit_view .add_form .el-form-item .el-form-item__label{
    width: 150px;
    background: none;
    text-align: right;
    display: block;
    font-size: 16px;
    color: rgb(51, 51, 51);
    font-weight: 500;
}
.edit_view .add_form .el-form-item .el-form-item__content{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    width: calc(100% - 120px);
}
.edit_view .add_form .el-form-item .el-form-item__content .list_inp{
    height: 36px;
    line-height: 36px;
    border: 1px solid rgb(221, 221, 221);
    padding: 0px 10px;
    width: 100%;
    box-sizing: border-box;
    background: rgb(255, 255, 255);
    font-size: 16px;
}

.edit_view .add_form .el-form-item .el-form-item__content .list_sel{
    line-height: 36px;
    border: 1px solid rgb(221, 221, 221);
    box-sizing: border-box;
    width: calc(100% - 0px);
    padding: 0px 10px;
    border-radius: 0px;
    background: rgb(255, 255, 255);
    font-size: 16px;
}









</style>